.header {
	margin-bottom: 55px;
	.container {
		padding: 0; } }

.header-nav {
	margin-right: 5.7%;
	margin-left: 20.6%;
	&__list {
		display: flex;
		align-items: center; }
	&__item {
		margin-right: 37px;
		&:last-child {
			margin-right: 0; }
		&__link {
			font-size: .875rem;
			font-weight: bold;
			line-height: 1.063rem; }
		.dropdown-menu {
			min-width: auto; } } }


.header-logo {
	&__img {
		max-width: 100%;
		height: auto; } }

.header-link {
	&__list {
		display: flex;
		margin-right: 2.5%; }
	&__item {
		&:not(:last-child) {
			margin-right: 25px; } } }

.header-phone {
	&__list {
		display: flex;
		align-items: center; }

	&__item {
		&:not(:last-child) {
			margin-right: 18px; }
		&__label {
			font-size: 0.625rem;
			line-height: 0.813rem;
			display: flex;
			align-items: center;
			color: var(--grey-text); }
		&__link {
			font-size: 0.813rem;
			line-height: 1rem;
			font-weight: bold;
			color: var(--black); } } }

.header-burger {
	display: flex;
	align-items: center;
	padding-right: 20px;
	&__link {} }

.header-logo {
	margin-right: 40px; }

.header-search {
	min-width: 355px;
	margin-right: 45px;
	&__form {
		width: 100%;
		max-width: 355px; }
	&__input {
		padding-right: 35px;
		border-radius: 25px;
		height: 35px !important; }
	&__button {
		background-color: var(--white);
		box-shadow: none;
		cursor: pointer; }
	&__icon {
		margin-left: -25px; } }

.header-bar {
	display: flex;
	&__item {
		display: inline-flex;
		align-items: center;
		margin-right: 20px;
		&__icon {
			margin-right: 6px; }
		&__link {
			font-size: 1rem;
			font-weight: bold;
			line-height: 1.188rem;
			color: var(--white);

			&:hover {
				color: var(--yellow); } } } }


.header-cart {
	display: flex;
	&__counter {
		display: flex;
		position: relative;
		margin-right: 15px;
		cursor: pointer; }
	&__icon {}
	&__number {
		width: 24px;
		height: 24px;
		display: flex;
		align-items: center;
		justify-content: center;

		border-radius: 100%;
		background-color: var(--white);

		font-weight: bold;
		font-size: .75rem;
		line-height: 120%;
		text-align: center;
		color: var(--vinishkotyan);

		margin-top: -9px;
		margin-left: -2px; }

	&__price {
		font-size: 18px;
		font-weight: bold;
		line-height: 120%;
		color: var(--white); } }



.header-top {
	background-color: #FAFBFD;
	position: relative;
	.grid-container {
		display: flex;
		align-items: flex-end;
		padding-top: 15px;
		padding-bottom: 12px; }

	.header-nav {
		&__item {
			color: var(--black); } } }

.header-main {
	position: relative;
	.grid-container {
		display: flex;
		align-items: center;
		background-color: var(--vinishkotyan);
		padding: 10px 30px; }

	.header-nav {
		margin-right: 50px;
		&__item {
			margin-right: 35px;
			.icon-menu {
				margin-left: 10px; } }
		&__link {
			font-size: 18px;
			font-weight: bold;
			line-height: 120%;
			color: var(--white); } }
